import {  mapState } from 'vuex'
export default {
	data(){
		return{
			title: '',
			isFixed: false
		}
	},
	computed:{
		...mapState(['systemInfo']),
		RankMarin(){ 
			return uni.upx2px(88)+this.systemInfo.statusBarHeight+'px'
		}
	},
	methods: {
	},
	onLoad(){
	},
	onPageScroll(e){
		if(e.scrollTop>uni.upx2px(250)){



			this.isFixed = true
			return
		}



		this.isFixed = false
		
	}
}
